/*=============================================
=            Font Family                      =
=============================================*/

/*-------- Font ElegantIcons Icon --------*/

@font-face {
    font-family: "elegant-icons";
    src: url(/moviesAppFeb/static/media/elegant-icons.78ba4ad1.eot);
    src: url(/moviesAppFeb/static/media/elegant-icons.78ba4ad1.eot?#iefix) format("eot"), url(/moviesAppFeb/static/media/elegant-icons.941bf29b.woff) format("woff"), url(/moviesAppFeb/static/media/elegant-icons.4555dd9c.ttf) format("truetype"),
        url(/moviesAppFeb/static/media/elegant-icons.a3709f85.svg#elegant-icons) format("svg");
}

.ei {
    text-decoration: none;
    text-transform: none;
    font-family: "elegant-icons";
    font-style: normal;
    font-feature-settings: normal;
    font-variant: normal;
    font-weight: normal;
    color: #444;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    speak: none;
}

/*=============================================
=            Global Style                      =
=============================================*/

body {
    color: #444;
    background: #efefef !important;
}

*,
::after,
::before {
    margin: 0;
    padding: 0;
}

/*=============================================
=         Component Navbar                    =
=============================================*/

.Navbar {
    z-index: 9999;
    height: 70px;
    margin-bottom: 10px;
    background-color: rgb(51, 51, 51);
}

.logo {
    font-family: "Indie Flower", cursive;
}

.dv-star-rating {
    margin: 9px;
}

.form-inline {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.input-search {
    height: 55px;
    padding: 12px 10px 10px 20px;
}

.input-search:focus {
    box-shadow: none !important;
}

.ei-icon_search {
    right: 15px;
}

.ei-icon_search:before {
    content: "\55";
}

/*=============================================
=            Component Banner                 =
=============================================*/

.Banner {
    height: 87vh;
    margin: 0 0 80px;
    border-radius: 5px;
    overflow: hidden;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(/moviesAppFeb/static/media/48809aaf53cfd24899f1cbf222db555c.4cfe508e.png) center center no-repeat;
    background-size: cover;
    -o-background-size: cover;
    -ms-border-radius: 5px;
    padding: 60px;
}

.banner small {
    font: 500 0.875rem "Poppins", Helvetica, sans-serif;
}

.banner h3 {
    margin: 10px 0 0 0;
    font: 500 2rem/1.2 "Poppins", Helvetica, sans-serif;
}

.banner p {
    font: 400 1.3125rem/1.5 "Poppins", Helvetica, sans-serif;
}

/*=============================================
=            Component Movies                 =
=============================================*/

/* Title Section */

.title-section {
    margin-bottom: 1em;
    padding: 0.3em 0.5em;
    border-left: 5px solid #d13008;
    text-transform: uppercase;
    font: 500 1.2rem/1.9 "Poppins", Helvetica, sans-serif;
    color: #212121;
}

/* Card */

.card-movie {
    position: relative;
    height: 100%;
    overflow: hidden;
    font: 500 0.8125rem/1.9 "Poppins", Helvetica, sans-serif;
    cursor: pointer;
}

.card-movie img {
    height: inherit;
}

.card-movie .card-ovr {
    position: absolute;
    height: 100%;
    padding: 6rem 1rem;
    font: 500 0.8125rem/1.7 "Poppins", Helvetica, sans-serif;
    background-image: linear-gradient(to top, rgba(2, 14, 51, 0.7) 10%, #132692);
    transition: all 0.3s ease-in-out;
    transform: translateY(100%);
}

.card-movie:hover .card-ovr {
    transform: translateY(-9%);
}

.footer-card-movie {
    bottom: -1px;
    padding-top: 15px;
    text-align: center;
    font: 500 0.8125rem/1 "Poppins", Helvetica, sans-serif;
    background-color: #040847;
}
